import React from 'react'
import { Link } from 'react-router-dom';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    // useParams,
  } from "react-router-dom";
  import { Navbar,Nav,NavDropdown,Form,FormControl,Button } from 'react-bootstrap'
//   import Home from './Home';
//   import AboutUs from './AboutUs';
//   import ContactUs from './ContactUs';
  import "./Nav.css"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSearch,  } from '@fortawesome/free-solid-svg-icons';
import log from './img/log.png'
import Home from './Home';
import AboutUs from './AboutUs';
import ContactUs from './ContactUs';
  

class BootstrapNavbar extends React.Component{

    render(){
        return(
            <div>
                 <div className="container-fluid flu">
                <div className="row">
                    <div className="col-md-12 col-lg-12 col-sm-12 ">
                       
                        <Router>
                            <Navbar className="Navbarr" bg="light" variant="light"  expand="lg" sticky="top">
                                <Navbar.Brand href="/"><img src={log} className="log" alt="loog" /></Navbar.Brand>
                                <Navbar.Toggle className="toggle" aria-controls="basic-navbar-nav" />
                                <Navbar.Collapse className="coll"  id="basic-navbar-nav">
                                    <Nav className="naved">
                                    <Link to="/easypaisa">Home</Link>
                                    <Link to="/contact-us">Contact Us</Link>
                                    <Link to="/about-us">About Us</Link>
                                    {/* <NavDropdown title="More" id="basic-nav-dropdown">
                                        <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                                        <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                                        <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                                        <NavDropdown.Divider />
                                        <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
                                    </NavDropdown> */}
                                    </Nav>
                                    {/* <Form inline>
                                    <FormControl type="text" placeholder="Search" className="mr-sm-2" />
                                    <Button  variant="outline-success" className="" >Search</Button>
                                    </Form> */}
                                </Navbar.Collapse>
                            </Navbar>
                            <Form inline className="navedd">
                                    <FormControl type="text" placeholder="Search" className="w-30 " />
                                    <Button  variant="outline-success" className="w-20" ><span>Search</span> <FontAwesomeIcon className="ico" icon={faSearch}  size="lg"   /></Button>
                                    </Form>
                            <br />
                            {/* <Switch>
                                <Route exact path="/">
                                    <Home />
                                </Route>
                                <Route path="/about-us">
                                    <AboutUs />
                                </Route>
                                <Route path="/contact-us">
                                    <ContactUs />
                                </Route>
                            </Switch> */}
                            <Switch>
                                <Route exact path="/easypaisa" component={Home} />
                                <Route path="/about-us" component={AboutUs} />
                                <Route path="/contact-us" component={ContactUs} />
                                </Switch>
                        </Router>
                    </div>
                    </div>
                </div>
            </div>
        
        
            )  
    }
}

export default BootstrapNavbar;